import Header from '../../components/Header';
import EntireNav from '../../components/EntrieNav';
import TwoLineContainer from '../../components/TwoLineContainer';
import NumberOfCharge from '../../components/NumberOfCharge';
import SortSelect from '../../components/SortSelect';
import Search from '../../components/Search';
import CalendarIcon from '../../components/CalendarIcon';
import SearchButton from '../../components/SearchButton';
import SortListContainer from '../../components/SortListContainer';
import StationList from '../../components/StationList';
import TotalUse from '../../components/TotalUse';
import TotalChargeTime from '../../components/TotalChargeTime';
import '../../css/charge_log.scss';

function ChargeLog(){
  return(
    <>
      <Header />
      <EntireNav />
      <div className="chargeLog_mainBoard">
        <div className="chargeLog_topBoard">
          <TwoLineContainer className="currentCharge" title="충전 현황">
            <NumberOfCharge />
          </TwoLineContainer>
          <TwoLineContainer className="totalUse" title="일일 총 이용횟수">
            <TotalUse />
          </TwoLineContainer>
          <TwoLineContainer className="totalChargeTime" title="일일 총 충전시간">
            <TotalChargeTime />
          </TwoLineContainer>
          <TwoLineContainer className="statistics" title="지표">

          </TwoLineContainer>
        </div>
        <div className="chargeLog_bottomBoard">
          <div className="chargeLog_searchContainer">
            <div className="sortSelectWrapper"><SortSelect /></div>
            <div className="searchWrapper"><Search /></div>
            <div className="calendarIconWrapper"><CalendarIcon /></div>
            <SearchButton />
          </div>
          <StationList />
        </div>
      </div>
    </>
  );
}

export default ChargeLog;