import React, { useEffect } from 'react';
import {
  Chart,
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
} from 'chart.js';
import { trueFunc } from 'boolbase';

Chart.register(
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
);

const today_last = window.today_last;
const today_this = window.today_this;

const canvas = <canvas className="totalChargeTime-canvas" style={{width: "95%", height: "95%"}}></canvas>;

function TotalChargeTime(){

	useEffect(() => {
		const ctx = document.querySelector('.totalChargeTime-canvas').getContext("2d");

		const chart = new Chart(ctx, {
			type: 'bar',
			data: {
				labels: [
					"04/01",
					"04/02",
					"19:26",
				],
				datasets: [
					{
						data: [300, today_last, today_this],
						backgroundColor: [
							'rgba(107, 107, 107, 100)',
							'rgba(107, 107, 107, 100)',
							'rgba(255, 206, 0, 100)',
						],
						barThickness: 14,
					}
				]
			},
			options: {
				layout: {
					padding: {
						right: 10
					}
				},
				plugins: {
					legend: {
						display: false
					}
				},
				responsive: false,
				scales: {
					x:{
						title:{
							display: true,
							text: '날짜',
							font: {
								family: 'Noto Sans KR',
								weight: 400,
								size: 13,
							},
							color: 'rgb(107, 107, 107)',
						}
					},
					y: {
						title:{
							display: true,
							text: '분',
							font: {
								family: 'Noto Sans KR',
								weight: 400,
								size: 13
							},
							color: 'rgb(107, 107, 107)',
						},
						beginAtZero: true,
					}
				},
			},
		});
	})

  return(
    <div className="totalChargeTime-container">
      {canvas}
    </div>
  );
}

export default TotalChargeTime;
