import React from 'react';

// const number = window.NumberOfCharge;
const number = 36000;

class NumberOfCharge extends React.Component{
  render(){
    return(
      <div className="chargeNumber-container">
        <div className="chargeNumber" style={{marginBottom: '22px'}}>
          {number}
        </div>
        <div>현재 충전 중</div>
      </div>
    )
  }
}

export default NumberOfCharge;