import React, { useEffect } from 'react';
import {
  Chart,
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
} from 'chart.js';

Chart.register(
  ArcElement,
  LineElement,
  BarElement,
  PointElement,
  BarController,
  BubbleController,
  DoughnutController,
  LineController,
  PieController,
  PolarAreaController,
  RadarController,
  ScatterController,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  RadialLinearScale,
  TimeScale,
  TimeSeriesScale,
  Decimation,
  Filler,
  Legend,
  Title,
  Tooltip
);

const month_last = window.month_last;
const month_this = window.month_this;

const canvas = <canvas className="totalUse-canvas" style={{width: "95%", height: "95%"}}></canvas>;

function TotalUse(){

	useEffect(() => {
		const ctx = document.querySelector('.totalUse-canvas').getContext("2d");

		const chart = new Chart(ctx, {
			type: 'bar',
			data: {
				labels: [
					"04/01",
					"04/02",
					"19:26",
				],
				datasets: [
					{
						data: [44326, month_last, month_this],
						backgroundColor: [
							'rgba(107, 107, 107, 100)',
							'rgba(107, 107, 107, 100)',
							'rgba(255, 206, 0, 100)',
						],
						barThickness: 14,
					}
				]
			},
			options: {
				layout: {
					padding: {
						right: 10
					}
				},
				plugins: {
					legend: {
						display: false
					}
				},
				responsive: false,
				scales: {
					x:{
						title:{
							display: true,
							text: '날짜',
							font: {
								family: 'Noto Sans KR',
								weight: 400,
								size: 13,
							},
							color: 'rgb(107, 107, 107)',
						}
					},
					y: {
						title:{
							display: true,
							text: '횟수',
							font: {
								family: 'Noto Sans KR',
								weight: 400,
								size: 13
							},
							color: 'rgb(107, 107, 107)',
						},
						beginAtZero: true,
					}
				},
			},
		});
	})

  return(
    <div className="totalUse-container">
      {canvas}
    </div>
  );
}

export default TotalUse;
