import React from 'react';
import { sortList } from '../data/sortList';

class SortListContainer extends React.Component{
  render(){
    const list = sortList.map((title, index) => (
      <li key={index}>{title}</li>
    ));

    return(
      <div className="sortListContainer">
        {list}
      </div>
    );
  }
}

export default SortListContainer;