import React from 'react';

class SubNav extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      list: {
        '관제시스템': ['메인현황판', '스테이션 현황'],
      }
    }
    this.getList = this.getList.bind(this);
  }

  getList(){
    const $list = [];
    const titles = ['메인현황판', '스테이션 현황', '충전 로그', '에러 로그'];

    for(let i = 0; i < titles.length; i++){
      $list.push(<li key={i}>{titles[i]}<div></div></li>);
    }

    return $list;
  }

  render(){
    return(
      <nav className='subNav'>
        <ul>
          {this.getList()}
        </ul>
      </nav>
    )
  }
}

export default SubNav;